0.08);\n          color: $telegram-color;\n        }\n      }\n\n      &.m-wa {\n        color: $wa-color;\n        &:hover {\n          background-color: rgba(0, 191, 165, 0.08);\n          color: $wa-color;\n        }\n      }\n\n      &.m-skype {\n        color: $skype-color;\n        &:hover {\n          background-color: rgba(0, 175, 240, 0.08);\n          color: $skype-color;\n        }\n      }\n\n      &.m-copy {\n        padding: 16px 10px;\n        font-size: 16px;\n      }\n\n      &.m-default:hover,\n      &.m-copy:hover {\n        background-color: rgba(0, 0, 0, 0.08);\n        color: $default-color;\n      }\n\n      &.m-by {\n        font-size: 9px;\n        margin-top: 0.8rem;\n        padding: 5px;\n        color: $default-color;\n        opacity: 0.55;\n        &:hover {\n          opacity: 1;\n        }\n      }\n    }\n\n    .w-500 {\n      font-weight: 500;\n    }\n  }\n\n  &.m-dark {\n    .m-modal-content {\n      background-color: #20232a;\n      &,\n      & p,\n      & p span,\n      & strong {\n        color: #fff;\n      }\n      a {\n        color: $default-dark-color;\n\n        &:not(.m-by):hover {\n          background-color: rgba(134, 134, 134, 0.08);\n          color: $default-dark-color;\n        }\n\n        &.m-gmail {\n          color: $gmail-dark-color;\n\n          &:hover {\n            background-color: rgba(212, 70, 56, 0.08);\n            color: $gmail-dark-color;\n          }\n        }\n\n        &.m-outlook {\n          color: $outlook-dark-color;\n\n          &:hover {\n            background-color: rgba(0, 114, 198, 0.08);\n            color: $outlook-dark-color;\n          }\n        }\n\n        &.m-tg {\n          color: $telegram-dark-color;\n\n          &:hover {\n            background-color: rgba(0, 136, 204, 0.08);\n            color: $telegram-dark-color;\n          }\n        }\n\n        &.m-wa {\n          color: $wa-dark-color;\n\n          &:hover {\n            background-color: rgba(0, 191, 165, 0.08);\n